import React from "react";
import {loadStorage} from  "../utils/Storage"

function CreateArea(props) {
  let [input, setInput] = React.useState({
    title: "",
    content: ""
  });

  function handleChange(event) {
    let { name, value } = event.target;
    setInput((previous) => {
      return {
        ...previous,
        [name]: value //only able to do this coz the state object keys have same names as JSX elements
      };
    });
  }

  async function handleClick(event) {
    const token=loadStorage('token');
    event.preventDefault();
    //accessing api
    const requestOptions={
      method:'POST',
      headers:{'Content-Type':'application/json',token},
      body:JSON.stringify({title:input.title,content:input.content})
    };
    fetch('/api/home',requestOptions)
    .then(res=>res.json)
    .then(data=>console.log(data));


    //lifting state to parent
    props.addNote(input);
    setInput({
      title: "",
      content: ""
    });
  }
  return (
    <div>
      <form>
        <input
          onChange={handleChange}
          name="title"
          placeholder="Title"
          value={input.title}
        />
        <textarea
          onChange={handleChange}
          name="content"
          placeholder="Take a note..."
          rows="3"
          value={input.content}
        />
        <button onClick={handleClick}>Add</button>
      </form>
    </div>
  );
}

export default CreateArea;
