import React from "react";
import {loadStorage} from "../utils/Storage"
function Note(props) {
  
async  function handleClick()
{ try{

  const jwt=loadStorage('token');
  const res=await fetch('/api/home',{method:'DELETE',
  headers:{'Content-Type':'application/json','token':jwt},
  body:JSON.stringify({index:props.id})
  })
  const data=await res.json();
  console.log(data);
  props.delete(props.id);
}
catch(e)
{
  console.log(e);
}
}

  return (
    <div className="note">
      <h1>{props.title}</h1>
      <p>{props.content}</p>
      <button onClick={handleClick}>DELETE</button>
    </div>
  );
}

export default Note;
