import React,{useEffect} from "react";
import {loadStorage,removeStorage} from "../utils/Storage"
import Note from "./Note";
import CreateArea from "./CreateArea";
import {Redirect} from "react-router-dom"
export default function Dashboard(props)
{

    
    let [notes, setNote] = React.useState([]);
useEffect(()=>{
    async function getNotes()
    {
        const token=loadStorage("token");

        const res=await fetch(`/api/home`,{
        method:'GET',
        headers:{
            token,
        }
            
        })
        const data=await res.json();
        if(!res.ok)
        {
            props.setAuthenticate(false);
        }
        else
        {
            props.setAuthenticate(true);
            setNote(data);
        }
    }
    getNotes();
},[]);



function addNote(newNote) {
    setNote((previous) => {
    return [...previous, newNote];
});
}

function deleteNote(deleteindex) {
  setNote((previous) => {
      console.log("delete in dashboard");
    return previous.filter((note, index) => {
      return index !== deleteindex;
    });
});
}
function handleLogout()
{
    props.setAuthenticate(false);
    removeStorage("token");
}

return (
    <div>
    {props.authenticate&&<button onClick={handleLogout}> Logout</button>}
   {!props.authenticate&&<Redirect to="/login"></Redirect>}
    <CreateArea addNote={addNote} />

    {notes.map((note, index) => {
        return (
        <Note
        key={index}
        id={index}
          title={note.title}
          content={note.content}
          delete={deleteNote}
          />
          );
    })}

   
  </div>
);
}