import React,{useState,useEffect} from "react";
import Header from "./Header";
import Footer from "./Footer";
import Dashboard from "./dashboard";
import Authenticate from "../authenticate";
import {Switch,Route} from "react-router-dom";
import {loadStorage} from "../utils/Storage"

function App() {
  const [authenticate,setAuthenticate]=useState(false);
  useEffect(()=>{
    async function getUser()
    {
      const token=loadStorage("token");
      if(token===null)
      {
        setAuthenticate(false);

      }
      else
      {
        setAuthenticate(true);
      }
    }
    getUser();


  },[])

  return<>
  <Header/> 
  <Switch>
    <Route exact path="/dashboard" ><Dashboard setAuthenticate={setAuthenticate} authenticate={authenticate}/></Route>
    <Route exact path ="/"><Authenticate type="register" setAuthenticate={setAuthenticate} authenticate={authenticate}/></Route>
    <Route exact path ="/login"><Authenticate type="login"setAuthenticate={setAuthenticate} authenticate={authenticate} /></Route>

  </Switch>
  <Footer/>
  </>
 
}

export default App;
