import React from "react";
import {Redirect,Link} from "react-router-dom";
import {setStorage} from "./utils/Storage";

function Authenticate(props)
{ 
    let [input,setInput]=React.useState({
    name:"",
    email:"",
    password:"",
    });
    function handleChange(event)
    {

        let {name,value}=event.target;
        setInput(previous=>{
            return {
                ...previous,
                [name]:value
            }
        })
        
    }
  

    async function handleClick(event)
    {
        event.preventDefault();
        const requestOptions={
            method:'POST',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({name:input.name,email:input.email,password:input.password})
        };
        const res=await fetch(`/api/${props.type}`,requestOptions);
        const data= await res.json();
        if(!res.ok)
        {
            props.setAuthenticate(false);
        }
        else
        {props.setAuthenticate(true);

            setStorage(data);
        }

        

    }

    return <div>
        {props.authenticate&&<Redirect to="/dashboard"></Redirect>}
        <form action="POST" >
       {props.type==="register"&&<div className="mb-3">
    <label className="form-label">Name</label>
    <input type="text" className="form-control" id="name" name="name" onChange={handleChange} value={input.name}/>
    
  </div>
  }

    <div className="mb-3">
    <label className="form-label">Email address</label>
    <input type="email" className="form-control" id="Email" name="email" onChange={handleChange} value={input.email}/>
   
  </div>
  <div className="mb-3">
    <label className="form-label">Password</label>
    <input type="password" className="form-control" id="Password" name="password"onChange={handleChange} value={input.password}/>
  </div>
  <button style={{width:"100px",borderRadius:"25px"}} onClick={handleClick} type="submit" className="btn btn-warning">Submit</button>
</form>

{props.type==="register"?<Link to="/login">Login</Link>:null}
</div>;




}
export default Authenticate;